





















import Vue from 'vue';
import draggable from 'vuedraggable';
import Track from '@/components/Track.vue';

export default Vue.extend({
  name: 'TrackList',
  props: ['trackList'],
  components: {
    draggable,
    Track,
  },
  data() {
    return {
      tracks: [],
    };
  },
  async created() {
    await this.trackList.fetchAllVideoInfo();
    this.tracks = this.trackList.getAllTracks();
  },
})
